import { computed, makeAutoObservable, observable, runInAction } from "mobx";
import { CUSTOM_EVENT } from "./patches";
import { StepState } from "./step.state";

class ProgressState {
    currentStepIndex = 0;
    steps: StepState[] = [];
    showCongratulations = false;

    get showingLevels() {
        return this.steps.slice(0, this.currentStepIndex + 1);
    }

    get activeLevel() {
        return this.steps[this.currentStepIndex];
    }

    constructor() {
        makeAutoObservable(this, {
            currentStepIndex: observable,
            showingLevels: computed
        });

        this.listenCustomEvents();
    }

    use(...steps: StepState[]) {
        this.steps = steps;
    }

    listenCustomEvents() {
        window.addEventListener(CUSTOM_EVENT.ALERT, (async (event: CustomEvent<string>) => {
            if (this.activeLevel.checkDone(event.detail)) {
                runInAction(() => {
                    this.showCongratulations = true;
                })
                setTimeout(() => {
                    runInAction(() => {
                        this.currentStepIndex++;
                        this.showCongratulations = false;
                    })
                }, 300);
            }
        }) as unknown as EventListener);
        window.addEventListener(CUSTOM_EVENT.LOG, () => {
            this.activeLevel.checkDone(arguments)
        })
    }
}

const progressState = new ProgressState();

export default progressState;