import './App.css';
import { observer } from 'mobx-react-lite'
import Step from './Step';
import progressState from './progress.state';
import { StepState } from './step.state';

progressState.use(
  new StepState('alert', <>
    <div>
      Метод <b>Window.alert()</b> показывает диалоговое окно с опциональным (необязательным) сообщением и кнопкой OK.
    </div>
    <code>
      window.alert(message);
    </code>
    <ul>
      <li>message это опциональная (необязательная) строка текста, которую вы хотите отобразить в диалоговом окне, или же объект, который будет преобразован в строку и отображён.</li>
    </ul>
    Вставьте в консоль следующий фрагмент кода и нажмите клавишу <kbd>Enter</kbd>
  </>, "alert('123')", (arg) => arg === '123'),
  new StepState('console.log', <>23</>, 'console')
);

const App = observer(() => {
  return (<>
    {progressState.showCongratulations && <Congratulations></Congratulations>}
    {progressState.showingLevels.map(step => <Step key={step.id} step={step} />)}
  </>)
});

const Congratulations = () => <div style={{
  position: "absolute",
  top: 0,
  left: 0,
  width: '100vw',
  height: '100vh',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  fontSize: '500px',
  zIndex: 999
}}>
  🥳
</div>;

export default App;
