export enum CUSTOM_EVENT {
    ALERT = 'alert',
    LOG = 'LOG'
}

(function () {
    const old_alert = window.alert;

    window.alert = function (str) {
        old_alert.apply(this, arguments as any);
        console.info(str);

        window.dispatchEvent(new CustomEvent(CUSTOM_EVENT.ALERT, { detail: str }));
    }

    const old_log = console.log;

    console.log = function (...params: unknown[]) {
        old_log.apply(this, [arguments]);
        console.info(arguments);

        window.dispatchEvent(new CustomEvent(CUSTOM_EVENT.LOG, { detail: params }));
    }
})()
