import { observer } from 'mobx-react-lite';
import { CopyBlock, solarizedLight } from 'react-code-blocks';

import { StepState } from "./step.state";


const Step = observer(({ step }: { step: StepState }) => {
    return (<>
        <h2>{step.id}</h2>
        {step.description}

        <CopyBlock text={step.code} language="javascript" theme={solarizedLight} />
    </>)
})

export default Step;